  fetchOneRecord(recordId) {
    return this.dataDB.find((record) => record.recordId === recordId);
  }

  delete(recordId) {
    const takeRecord = this.fetchOneRecord(recordId);
    const index = this.dataDB.indexOf(takeRecord);
    this.dataDB.splice(index, 1);
    return {};
  }
